import * as React from 'react';
import { Button } from '@material-ui/core';
import { GlobalFunction } from './GlobalFunction';

interface IProps {
    lastSave?: Date;
    forceShowResolveButton?: boolean;

    onSaveForNow?: () => void;
    onSubmitTicket?: () => void;
    onResolveTicket?: () => void;

    onAssign?: () => void;
    onEscalate?: () => void;
    onUpdateTicket?: () => void;
    onFinalAssessment?: () => void;
}

export class TicketFooter extends React.Component<IProps> {

    interval?: NodeJS.Timeout;

    componentDidMount() {
        // setInterval()
        this.interval = setInterval(() => this.setState({ time: Date.now() }), 5000);
    }

    

    render(): JSX.Element {
        return (
            <div className='footer'>

                {this.props.onSaveForNow ? (
                    <Button 
                        className='button-transparent' 
                        variant='outlined'
                        onClick={this.props.onSaveForNow}>
                        <div style={{display: 'block'}}>
                            <div className='text'>Save For Now</div>
                            {this.props.lastSave ? (
                                <div className='small-text'>
                                    {`Auto-saved ${GlobalFunction.getTimeDeffirence(this.props.lastSave, new Date())} ago`}
                                </div>
                            ) : null}
                        </div>
                    </Button>
                ) : null}

                {this.props.onResolveTicket && this.props.forceShowResolveButton ? (
                    <Button 
                        className='button'
                        onClick={this.props.onResolveTicket}>
                        Mark as Resolved
                    </Button>
                ) : null}
                
                {this.props.onSubmitTicket ? (
                    <Button 
                        className='button'
                        onClick={this.props.onSubmitTicket}>
                        Submit Ticket
                    </Button>
                ) : null}

                {this.props.onAssign ? (
                    <Button 
                        className='button'
                        color='default'
                        onClick={this.props.onAssign}>
                        Assign
                    </Button>
                ) : null}

                {this.props.onEscalate ? (
                    <Button 
                        className='button-error'
                        onClick={this.props.onEscalate}>
                        Escalate
                    </Button>
                ) : null}

                {this.props.onUpdateTicket ? (
                    <Button 
                        className='button'
                        onClick={this.props.onUpdateTicket}>
                        Update Ticket
                    </Button>
                ) : null}

                {this.props.onFinalAssessment ? (
                    <Button 
                        className='button'
                        onClick={this.props.onFinalAssessment}>
                        Proceed with Final Assessment
                    </Button>
                ) : null}
            </div>
        );
    }
}